import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { PageEvent } from '@angular/material/paginator';
import { ToastrServiceLocal } from 'src/app/services/toast/toastr.service';
import Swal from 'sweetalert2';
import { RepartoFacadeService } from './reparto-facade.service';

@Component({
  selector: 'app-reparto',
  templateUrl: './reparto.component.html',
  styleUrls: ['./reparto.component.css']
})
export class RepartoComponent implements OnInit {
  public buscar = new FormControl("");// Se llama cada vez que se escribe
  public formReparto: FormGroup;
  public pageSize = 10;
  public page = 0;
  public pageEvent: PageEvent;
  public pageIndex: number = 0;
  public desde = 0;
  public hasta = 10;
  
  constructor(public repartoFacade: RepartoFacadeService, public dialog: MatDialog,
    public toast: ToastrServiceLocal) {
    this.repartoFacade.MostrarReparto("0");
  }

  ngOnInit(): void {
  }

  openDialog(template, params?) {
    const dialogRef = this.dialog.open(template, {
      panelClass: "app-full-bleed-dialog", //Agregar una clase ccs al dialogo
      disableClose: true
    });
    this.formReparto = new FormGroup({
      //Valores de front para insertar tipo de pedido
      Id: new FormControl(params?.Id || "0"),
      NombreReparto: new FormControl(params?.NombreReparto || "", [Validators.required]),
      Descripcion: new FormControl(params?.Descripcion || ""),
      Telefono: new FormControl(params?.Telefono || ""),
      usuario: new FormControl("ymunoz"),
      idEstado: new FormControl(params?.IdEstado || ""),
    });
  }
  Guardar() {
    if (this.formReparto.invalid) {
      this.toast.mensajeWarning("Es requerido ingresar los campos validos", "");
      this.formReparto.markAllAsTouched();
      return
    }
    //Accediendo al valor del control
    if (this.formReparto.get("Id").value === "0") {
      this.repartoFacade.InsertarReparto(this.formReparto.value, (respuesta) => {
        this.repartoFacade.MostrarReparto("0");
        this.dialog.closeAll();
      })
    }
    else {
      this.repartoFacade.ActualizarReparto(this.formReparto.value, (respuesta) => {
        this.repartoFacade.MostrarReparto("0");
        this.dialog.closeAll();
      })
    }
  }

  Eliminar(params) {
    Swal.fire({
      title: "Confirmacin",
      html: ` <p> Esta seguro quiere inhabilitar el metodo de pago <b>${params.Reparto}</b>? </p>`,
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#003399",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirmar",
      cancelButtonText: "Cancelar",
    }).then((result) => {
      if (result.isConfirmed) {
        this.repartoFacade.EliminarReparto(params.Id, (respuesta) => {
          this.repartoFacade.MostrarReparto("0");

        })
      }
    });
  }
  next(event: PageEvent) {

    if (event.pageIndex === this.pageIndex + 1) {
      this.desde = this.desde + this.pageSize;
      this.hasta = this.hasta + this.pageSize;
    }
    else if (event.pageIndex === this.pageIndex - 1) {
      this.desde = this.desde - this.pageSize;
      this.hasta = this.hasta - this.pageSize;
    }
    this.pageIndex = event.pageIndex;
  }

}
