import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class RepartoFacadeService {
  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private Reparto$ = new BehaviorSubject<any[]>([]);
  //Este va en el HTML para acceder desde otro componente
  public responseReparto$: Observable<any[]> = this.Reparto$.asObservable();



  constructor(public dataApi:DataApiService, public _mensajesHttp:MensajesHttpService) { }

  
  MostrarReparto(params: any) {
    this.Cargando$.next(true);
    this.Reparto$.next([]);
    const request$ = this.dataApi.GetDataApi(`mantenimiento/reparto/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.Reparto$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Reparto$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar el reparto', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  InsertarReparto(params: any, respuesta) {
    this.Cargando$.next(true);
    this.Reparto$.next([]);
    const request$ = this.dataApi.PostDataApi(`mantenimiento/reparto/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Reparto$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al insertar el reparto', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarReparto(params: any, respuesta) {
    this.Cargando$.next(true);
    this.Reparto$.next([]);
    const request$ = this.dataApi.PutDataApi(`mantenimiento/reparto/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Reparto$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar el reparto', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  EliminarReparto(params: any, respuesta) {
    this.Cargando$.next(true);
    this.Reparto$.next([]);
    const request$ = this.dataApi.DeleteDataApiUrl(`mantenimiento/reparto/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Reparto$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al eliminar el reparto', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }
}
