import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { GeneroComponent } from "./genero/genero.component";
import { MetodoPagoComponent } from "./metodo-pago/metodo-pago.component";
import { PedidosComponent } from "./pedidos/pedidos.component";
import { RepartoComponent } from "./reparto/reparto.component";
import { TipoContactoComponent } from "./tipo-contacto/tipo-contacto.component";
import { TipoIdentificacionComponent } from "./tipo-identificacion/tipo-identificacion.component";
import { TipoPedidoComponent } from "./tipo-pedido/tipo-pedido.component";

const routes: Routes = [
  {
    path: "tipoPedido",
    component: TipoPedidoComponent,
  },
  {
    path: "metodoPago",
    component: MetodoPagoComponent,
  },
  {
    path: "reparto",
    component: RepartoComponent,
  },
  {
    path: "pedidos",
    component: PedidosComponent,
  },
  {
    path: "tipoIdentificacion",
    component: TipoIdentificacionComponent,
  },
  {
    path: "genero",
    component: GeneroComponent,
  },
  {
    path: "tipoContacto",
    component: TipoContactoComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdministracionRoutingModule {}
