import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { PageEvent } from '@angular/material/paginator';
import { ToastrServiceLocal } from 'src/app/services/toast/toastr.service';
import Swal from 'sweetalert2';
import { PedidosFacadeService } from './pedidos-facade.service';

@Component({
  selector: 'app-pedidos',
  templateUrl: './pedidos.component.html',
  styleUrls: ['./pedidos.component.css']
})
export class PedidosComponent implements OnInit {
  public buscar = new FormControl("");// Se llama cada vez que se escribe
  public formPedido: FormGroup;
  public pageSize = 10;
  public page = 0;
  public pageEvent: PageEvent;
  public pageIndex: number = 0;
  public desde = 0;
  public hasta = 10;
  constructor(public pedidosFacade:PedidosFacadeService,public dialog: MatDialog,
    public toast: ToastrServiceLocal) {
      this.pedidosFacade.MostrarPedido("0");
      this.pedidosFacade.MostrarTipoPedidos("0");
      this.pedidosFacade.MostrarMetodosPago("0");
      this.pedidosFacade.MostrarReparto("0");
      this.pedidosFacade.MostrarUsuario("0");
      this.pedidosFacade.MostrarEstadosProceso("");
     }

  ngOnInit(): void {
  }
  openDialog(template, params?) {
    this.formPedido = new FormGroup({
      //Valores de front para insertar tipo de pedido
      Id: new FormControl(params?.Id || "0"),
      IdPedido: new FormControl(params?.Id || "0"),
      IdTipoPedido: new FormControl(params?.IdTipoPedido || "0"),
      IdMetodoPago: new FormControl(params?.IdMetodoPago || "0"),
      IdReparto: new FormControl(params?.IdReparto || null),
      IdUsuario: new FormControl(params?.IdUsuario || "0"),
      DetallePedido: new FormControl(params?.DetallePedido || "", [Validators.required]),
      Observacion: new FormControl(params?.Observacion || ""),
      usuario: new FormControl(""),
      idEstado: new FormControl(params?.IdEstado || 1 ),
      url: new FormControl(params?.Url || null)
    });

    const dialogRef = this.dialog.open(template, {
      panelClass: "app-full-bleed-dialog", //Agregar una clase ccs al dialogo,
      width: "90vw",
      height: "90vh",
      disableClose: true
    });
   
  }

  Guardar() {
    if (this.formPedido.invalid) {
      this.toast.mensajeWarning("Es requerido ingresar los campos validos", "");
      this.formPedido.markAllAsTouched();
      return
    }
    //Accediendo al valor del control
    if (this.formPedido.get("Id").value === "0") {
      this.pedidosFacade.InsertarPedido(this.formPedido.value, (respuesta) => {
        this.pedidosFacade.MostrarPedido("0");
        this.dialog.closeAll();
      })
    }
    else {
      this.pedidosFacade.ActualizarPedido(this.formPedido.value, (respuesta) => {
        this.pedidosFacade.MostrarPedido("0");
        this.dialog.closeAll();
      })
    }
  }
  Eliminar(params) {
    Swal.fire({
      title: "Confirmacin",
      html: ` <p> Esta seguro quiere inhabilitar el pedido ? </p>`,
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#003399",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirmar",
      cancelButtonText: "Cancelar",
    }).then((result) => {
      if (result.isConfirmed) {
        this.pedidosFacade.EliminarPedido(params.Id, (respuesta) => {
          this.pedidosFacade.MostrarPedido("0");

        })
      }
    });
  }
  next(event: PageEvent) {

    if (event.pageIndex === this.pageIndex + 1) {
      this.desde = this.desde + this.pageSize;
      this.hasta = this.hasta + this.pageSize;
    }
    else if (event.pageIndex === this.pageIndex - 1) {
      this.desde = this.desde - this.pageSize;
      this.hasta = this.hasta - this.pageSize;
    }
    this.pageIndex = event.pageIndex;
  }

  descargarImagen(){
    window.open(this.formPedido.value.url);
  }

}
