import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class MetodoPagoFacadeService {
  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private MetodoPago$ = new BehaviorSubject<any[]>([]);
  //Este va en el HTML para acceder desde otro componente
  public responseMetodoPago$: Observable<any[]> = this.MetodoPago$.asObservable();



  constructor(public dataApi:DataApiService, public _mensajesHttp:MensajesHttpService) { }

  
  MostrarMetodoPago(params: any) {
    this.Cargando$.next(true);
    this.MetodoPago$.next([]);
    const request$ = this.dataApi.GetDataApi(`mantenimiento/metodoPago/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.MetodoPago$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.MetodoPago$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar el metodo de pago', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  InsertarMetodoPago(params: any, respuesta) {
    this.Cargando$.next(true);
    this.MetodoPago$.next([]);
    const request$ = this.dataApi.PostDataApi(`mantenimiento/metodoPago/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.MetodoPago$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al insertar el metodo de pago', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarMetodoPago(params: any, respuesta) {
    this.Cargando$.next(true);
    this.MetodoPago$.next([]);
    const request$ = this.dataApi.PutDataApi(`mantenimiento/metodoPago/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.MetodoPago$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar el metodo de pago', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  EliminarMetodoPago(params: any, respuesta) {
    this.Cargando$.next(true);
    this.MetodoPago$.next([]);
    const request$ = this.dataApi.DeleteDataApiUrl(`mantenimiento/metodoPago/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.MetodoPago$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al eliminar el metodo de pago', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

}
