import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class TipoContactoFacadeService {

  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private TipoContacto$ = new BehaviorSubject<any[]>([]);
  public responseTipoContacto$: Observable<any[]> = this.TipoContacto$.asObservable();



  constructor(public dataApi:DataApiService, public _mensajesHttp:MensajesHttpService) { }

  
  MostrarTipoContacto(params: any) {
    this.Cargando$.next(true);
    this.TipoContacto$.next([]);
    const request$ = this.dataApi.GetDataApi(`personas/tiposContacto/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.TipoContacto$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.TipoContacto$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los tipos de contacto', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  InsertarTipoContacto(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.PostDataApi(`personas/tiposContacto/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al insertar el tipo contacto', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarTipoContacto(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.PutDataApi(`personas/tiposContacto/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar el tipo contacto', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  EliminarTipoContacto(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.DeleteDataApiUrl(`personas/tiposContacto/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al eliminar el tipo de contacto', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }
}
