import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class TipoIdentificacionFacadeService {

  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private TipoIdentificacion$ = new BehaviorSubject<any[]>([]);
  //Este va en el HTML para acceder desde otro componente
  public responseTipoIdentificacion$: Observable<any[]> = this.TipoIdentificacion$.asObservable();



  constructor(public dataApi:DataApiService, public _mensajesHttp:MensajesHttpService) { }

  
  MostrarTipoIdentificacion(params: any) {
    this.Cargando$.next(true);
    this.TipoIdentificacion$.next([]);
    const request$ = this.dataApi.GetDataApi(`personas/tipoIdentificacion/`, params).pipe(
      tap((result: DataApi | any) => {

        this.Cargando$.next(false);
        this.TipoIdentificacion$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.TipoIdentificacion$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los tipos de identificacin', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  InsertarTipoIdentificacion(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.PostDataApi(`personas/tipoIdentificacion/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al insertar el tipo de identificacin', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarTipoIdentificacion(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.PutDataApi(`personas/tipoIdentificacion/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar el tipo de identificacin', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  EliminarTipoIdentificacion(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.DeleteDataApiUrl(`personas/tipoIdentificacion/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al eliminar el tipo de identificacin', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }
}
