import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { PageEvent } from '@angular/material/paginator';
import { ToastrServiceLocal } from 'src/app/services/toast/toastr.service';
import Swal from 'sweetalert2';
import { GeneroFacadeService } from './genero-facade.service';

@Component({
  selector: 'app-genero',
  templateUrl: './genero.component.html',
  styleUrls: ['./genero.component.css']
})
export class GeneroComponent implements OnInit {

  public buscar = new FormControl(''); 
  public formGenero: FormGroup;
  public pageSize = 10;
  public page = 0;
  public pageEvent: PageEvent;
  public pageIndex: number = 0;
  public desde = 0;
  public hasta = 10;

  constructor(public generoFacade: GeneroFacadeService, public dialog: MatDialog, public toast:ToastrServiceLocal) { 
    this.generoFacade.MostrarGenero('0');
  }

  ngOnInit(): void {
  }

  openDialog(template, item?){
    this.formGenero = new FormGroup({
      id: new FormControl(item?.Id || 0 ),
      genero: new FormControl(item?.Genero || '', [Validators.required] ),
      idEstado: new FormControl(item?.IdEstado || ''),
    });

    const dialogRef = this.dialog.open(template,{
      panelClass: "app-full-bleed-dialog", //Agregar una clase ccs al dialogo,
      width: '40%',
      disableClose:true
    });

  }

  Guardar(){
    if(this.formGenero.invalid){
      this.toast.mensajeWarning('Es requerido ingresar los campos marcados como obligatorios', '');
      this.formGenero.markAllAsTouched();
      return;
    }

    if(this.formGenero.get('id').value === 0){
      this.generoFacade.InsertarGenero(this.formGenero.value, (respuesta)=>{
        if(respuesta.hasError === false){
          this.generoFacade.MostrarGenero('0');
          this.dialog.closeAll();
        }
      });
    }else{
      this.generoFacade.ActualizarGenero(this.formGenero.value, (respuesta)=>{
        if(respuesta.hasError === false){
          this.generoFacade.MostrarGenero('0');
          this.dialog.closeAll();
        }
      });
    }
  }

  Eliminar(params){
    Swal.fire({
      title: "Confirmacin",
      html: ` <p> Esta seguro quiere inhabilitar el genero <b>${params.Genero}</b>? </p>`,
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#003399",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirmar",
      cancelButtonText: "Cancelar",
    }).then((result) => {
      if (result.isConfirmed) {
      this.generoFacade.EliminarGenero(params.Id,(respuesta)=>{
        this.generoFacade.MostrarGenero("0");
        
      })
      }
    });
  }

  next(event: PageEvent) {

    if (event.pageIndex === this.pageIndex + 1) {
      this.desde = this.desde + this.pageSize;
      this.hasta = this.hasta + this.pageSize;
    }
    else if (event.pageIndex === this.pageIndex - 1) {
      this.desde = this.desde - this.pageSize;
      this.hasta = this.hasta - this.pageSize;
    }
    this.pageIndex = event.pageIndex;
  }

}
