import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class PedidosFacadeService {
  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private Pedidos$ = new BehaviorSubject<any[]>([]);
  //Este va en el HTML para acceder desde otro componente
  public responsePedidos$: Observable<any[]> = this.Pedidos$.asObservable();
  
  private TipoPedidos$ = new BehaviorSubject<any[]>([]);
  public responseTipoPedidos$: Observable<any[]> = this.TipoPedidos$.asObservable();

  private MetodosPago$ = new BehaviorSubject<any[]>([]);
  public responseMetodosPago$: Observable<any[]> = this.MetodosPago$.asObservable();

  private Reparto$ = new BehaviorSubject<any[]>([]);
  //Este va en el HTML para acceder desde otro componente
  public responseReparto$: Observable<any[]> = this.Reparto$.asObservable();

  private Usuario$ = new BehaviorSubject<any[]>([]);
  //Este va en el HTML para acceder desde otro componente
  public responseUsuario$: Observable<any[]> = this.Usuario$.asObservable();

  private EstadoProceso$ = new BehaviorSubject<any[]>([]);
  public responseEstadoProceso$: Observable<any[]> = this.EstadoProceso$.asObservable();

  constructor(public dataApi:DataApiService, public _mensajesHttp:MensajesHttpService) { }

  
  MostrarPedido(params: any) {
    this.Cargando$.next(true);
    this.Pedidos$.next([]);
    const request$ = this.dataApi.GetDataApi(`pedido/pedido/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.Pedidos$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Pedidos$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar el pedido', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  InsertarPedido(params: any, respuesta) {
    this.Cargando$.next(true);
    this.Pedidos$.next([]);
    const request$ = this.dataApi.PostDataApi(`pedido/pedido/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Pedidos$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al insertar el pedido', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarPedido(params: any, respuesta) {
    this.Cargando$.next(true);
    this.Pedidos$.next([]);
    const request$ = this.dataApi.PutDataApi(`pedido/pedido/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Pedidos$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar el pedido', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  EliminarPedido(params: any, respuesta) {
    this.Cargando$.next(true);
    this.Pedidos$.next([]);
    const request$ = this.dataApi.DeleteDataApiUrl(`pedido/pedido/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Pedidos$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al eliminar el pedido', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  MostrarTipoPedidos(params: any) {
    this.Cargando$.next(true);
    this.TipoPedidos$.next([]);
    const request$ = this.dataApi.GetDataApi(`mantenimiento/tipoPedido/`, params).pipe(
      tap((result: DataApi | any) => {
        console.log(result)
        this.Cargando$.next(false);
        this.TipoPedidos$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.TipoPedidos$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los tipos de pedidos', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  MostrarMetodosPago(params: any) {
    this.Cargando$.next(true);
    this.MetodosPago$.next([]);
    const request$ = this.dataApi.GetDataApi(`mantenimiento/metodoPago/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.MetodosPago$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.MetodosPago$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los metodos de pago', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  MostrarReparto(params: any) {
    this.Cargando$.next(true);
    this.Reparto$.next([]);
    const request$ = this.dataApi.GetDataApi(`mantenimiento/reparto/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.Reparto$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Reparto$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar el reparto', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  MostrarUsuario(params: any) {
    this.Cargando$.next(true);
    this.Usuario$.next([]);
    const request$ = this.dataApi.GetDataApi(`seguridad/usuario/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.Usuario$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Usuario$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar el usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  MostrarEstadosProceso(params: any) {
    this.Cargando$.next(true);
    this.Pedidos$.next([]);
    const request$ = this.dataApi.GetDataApi(`mantenimiento/mostrarEstadoProceso`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.EstadoProceso$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Pedidos$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los pedidos', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

}
