import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class GeneroFacadeService {

  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private Genero$ = new BehaviorSubject<any[]>([]);
  //Este va en el HTML para acceder desde otro componente
  public responseGenero$: Observable<any[]> = this.Genero$.asObservable();



  constructor(public dataApi:DataApiService, public _mensajesHttp:MensajesHttpService) { }

  
  MostrarGenero(params: any) {
    this.Cargando$.next(true);
    this.Genero$.next([]);
    const request$ = this.dataApi.GetDataApi(`personas/genero/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.Genero$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Genero$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los generos', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  InsertarGenero(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.PostDataApi(`personas/genero/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al insertar el genero', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarGenero(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.PutDataApi(`personas/genero/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar el genero', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  EliminarGenero(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.DeleteDataApiUrl(`personas/genero/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al eliminar el genero', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }
}
