import { Template } from '@angular/compiler/src/render3/r3_ast';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { PageEvent } from '@angular/material/paginator';
import { PaginatorService } from 'src/app/services/paginator.service';
import { ToastrServiceLocal } from 'src/app/services/toast/toastr.service';
import Swal from 'sweetalert2';
import { TipoPedidoFacadeService } from './tipo-pedido-facade.service';

@Component({
  selector: 'app-tipo-pedido',
  templateUrl: './tipo-pedido.component.html',
  styleUrls: ['./tipo-pedido.component.css']
})
export class TipoPedidoComponent implements OnInit {
  public buscar = new FormControl("");// Se llama cada vez que se escribe
  public formTipoPedido:FormGroup;
  public pageSize = 10;
  public page = 0;
  public pageEvent: PageEvent;
  public pageIndex: number = 0;
  public desde = 0;
  public hasta = 10;

  //El alias del FACADE debe ir en el HTML 
  constructor(public tipoPedidoFacade:TipoPedidoFacadeService, public paginatorService:PaginatorService,
    public dialog:MatDialog, public toast:ToastrServiceLocal) 
    {
    //Llama al metodo mostrar para que este cargando al iniciar  
    this.tipoPedidoFacade.MostrarTipoPedidos("0");
   }

  ngOnInit(): void {
  }
  //Modal
  openDialog(template, params?) {
    const dialogRef = this.dialog.open(template,{
      panelClass: "app-full-bleed-dialog", //Agregar una clase ccs al dialogo
      disableClose:true
    });
    this.formTipoPedido = new FormGroup({
      //Valores de front para insertar tipo de pedido
      id:new FormControl(params?.Id || "0"),
      tipoPedido:new FormControl(params?.TipoPedido ||"",[Validators.required]),
      descripcion:new FormControl(params?.Descripcion ||""),
      usuario:new FormControl("ymunoz"),
      idEstado:new FormControl(params?.IdEstado || ""),
    });
    
  }

  Guardar(){
    if(this.formTipoPedido.invalid){
      this.toast.mensajeWarning("Es requerido ingresar los campos validos", "");
      this.formTipoPedido.markAllAsTouched();
      return
    }
    //Accediendo al valor del control
    if(this.formTipoPedido.get("id").value === "0"){
      this.tipoPedidoFacade.InsertarTipoPedidos(this.formTipoPedido.value, (respuesta)=>{
        this.tipoPedidoFacade.MostrarTipoPedidos("0");
        this.dialog.closeAll();
      })
    }
    else{
      this.tipoPedidoFacade.ActualizarTipoPedidos(this.formTipoPedido.value, (respuesta)=>{
        this.tipoPedidoFacade.MostrarTipoPedidos("0");
        this.dialog.closeAll();
      })
    } 
  }

  Eliminar(params){
    Swal.fire({
      title: "Confirmacin",
      html: ` <p> Esta seguro quiere inhabilitar el Tipo de Pedido <b>${params.TipoPedido}</b>? </p>`,
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#003399",
      cancelButtonColor: "#d33",
      confirmButtonText: "Confirmar",
      cancelButtonText: "Cancelar",
    }).then((result) => {
      if (result.isConfirmed) {
      this.tipoPedidoFacade.EliminarTipoPedidos(params.Id,(respuesta)=>{
        this.tipoPedidoFacade.MostrarTipoPedidos("0");
        
      })
      }
    });
  }
next(event: PageEvent) {

    if (event.pageIndex === this.pageIndex + 1) {
      this.desde = this.desde + this.pageSize;
      this.hasta = this.hasta + this.pageSize;
    }
    else if (event.pageIndex === this.pageIndex - 1) {
      this.desde = this.desde - this.pageSize;
      this.hasta = this.hasta - this.pageSize;
    }
    this.pageIndex = event.pageIndex;
  }
}
